export default {
	data() {
		return {
			          // 点击删除收货地址出现背景色展示
						display:"none",
						
					  //编辑收货地址数据信息
					    c_editAddressName:"王某某",
						c_editAddressPhoneNumber:"198 8909 9087",
						c_editAddressLocal:"上海 上海市 浦东新区",
						c_editAddressDetailed:"商城路738号胜康廖氏大厦807"
		}
	},
	onLoad() {

	},
	methods: {
		        c_editAddressDeleteClick(){
					this.display="block";
					var that=this;
					 wx.showModal({
					 	content:"您确定要删除该收货地址吗？",
						confirmColor:"#FFB700",
						  success (res) {
						if (res.confirm) {
						  console.log('用户点击确定');
						  that.display="none";
						} else if (res.cancel) {
						  console.log('用户点击取消');
						  that.display="none";
						}
					   }
				   })
				}
                
	}
}
